/* Custon Style */
.main-header {
  height: 100vh;
  background-image: url(../img/header.jpg);
  background-size: cover;
  background-position: right bottom;
  background-repeat: no-repeat;
}

.navbar {
  position: relative;
  padding-top: 88px;
  padding-right: 15%;
  padding-left: 15%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  width: 80px;
  height: 80px;
}

.header-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu-button {
  display: block;
  width: 28px;
  height: 22px;
  cursor: pointer;
}

.menu-button .icon {
  position: relative;
  display: block;
}

.menu-button .icon::before {
  content: "";
  position: absolute;
  left: 0;
  top: 9px;
}

.menu-button .icon::after {
  content: "";
  position: absolute;
  left: 0;
  top: 18px;
}

.menu-button .icon,
.menu-button .icon::after,
.menu-button .icon::before {
  width: 100%;
  height: 4px;
  background-color: #000;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 6px;
}

.menu-button.on .icon {
  background-color: transparent;
}

.menu-button.on .icon::before {
  transform: rotate(45deg) translate(-1px, 0px);
}

.menu-button.on .icon::after {
  transform: rotate(-45deg) translate(6px, -7px);
}

.menu-title {
  margin-top: 8px;
  font-size: 12px;
  text-transform: uppercase;
}

.menu-box {
  position: absolute;
  display: none;
  padding: 30px 0;
  top: 180px;
  right: 15%;
  width: 250px;
  background-color: #42b7c8;
  opacity: .8;
  z-index: 1000;
  overflow: auto;
}

.menu-item {
  display: block;
  padding: 10px 0;
  text-align: center;
  font-weight: 300;
  color: #000;
  text-decoration: none;
}

.menu-item:hover {
  text-decoration: underline;
}

.header-content {
  margin-top: 202px;
  margin-right: 15%;
  margin-left: 15%;
}

.header-content h1 {
  margin: 0;
  font-size: 72px;
  font-weight: 300;
  color: #000;
}

.header-content h1 span {
  display: block;
}

.content-text {
  margin: 0;
  margin-top: 26px;
  font-size: 30px;
  font-weight: 300;
}

.content-text b {
  font-weight: 700;
}

.content-text-top {
  margin-top: 16px;
}

.about {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 500px;
  background-color: #f3f1ef;
}

.photo-hero {
  grid-column: 1 / 3;
  background-image: url(../img/photo_1.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.text-hero {
  grid-column: 3 / 5;
  padding-top: 112px;
  padding-right: 15%;
  padding-left: 7.5%;
}

.text-hero h2 {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  color: #666666;
  text-transform: uppercase;
}

.text-hero p {
  margin: 0;
  margin-top: 42px;
  font-size: 36px;
  font-weight: 300;
  color: #666666;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 515px 515px;
  background-color: #3f729b;
}

.photo-1, .photo-2, .photo-3, .photo-4 {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.photo-1 {
  background-image: url(../img/photo_2-1.jpg);
}

.photo-2 {
  background-image: url(../img/photo_2-2.jpg);
}

.photo-3 {
  background-image: url(../img/photo_2-3.jpg);
}

.photo-4 {
  background-image: url(../img/photo_2-4.jpg);
}

.instagram {
  grid-column: span 2;
  padding-top: 126px;
  padding-right: 11.25%;
  padding-left: 26.5%;
}

.instagram h2 {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  color: #f9f9f9;
  text-transform: uppercase;
}

.instagram p {
  margin: 0;
  margin-top: 42px;
  font-size: 36px;
  font-weight: 300;
  color: #f9f9f9;
}

.instagram .button {
  display: inline-block;
  margin-top: 38px;
