    font-size: 16px;
  }
  .box-two {
    width: 100%;
    display: flex;
  }
  .box-two-contact {
    width: 60%;
  }
  .box-two-follow {
    margin-top: 0;
  }
  .box-three {
    width: 100%;
    margin-top: 70px;
  }
  .box-three-sponsor {
    margin-top: 70px;
  }
}

@media only screen and (max-width: 480px) {
  .main-header {
    height: 500px;
  }
  .header-logo {
    width: 60px;
    height: 60px;
  }
  .header-logo img {
    width: 60px;
    height: 60px;
  }
  .header-content {
    margin-top: 80px;
  }
  .header-content h1 {
    font-size: 42px;
  }
  .content-text {
    font-size: 18px;
  }
  .content-text-top {
    margin-top: 10px;
  }
  .about {
    grid-template-rows: 300px 300px;
  }
  .text-hero p {
    margin-top: 26px;
    font-size: 22px;
  }
  .gallery {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, 300px);
  }
  .instagram {
    grid-column: span 1;
  }
  .instagram p {
    margin-top: 26px;
    font-size: 22px;
  }
  .music {
    grid-template-rows: 350px repeat(2, 300px);
  }
  .play ul {
    margin-top: 26px;
  }
  .spotify p {
    margin-top: 26px;
    font-size: 22px;
  }
  .itunes p {
    margin-top: 26px;
    font-size: 22px;
  }
  .box-two {
    flex-direction: column;
  }
  .box-two-contact {
    width: 100%;
  }
  .box-two-follow {
    width: 100%;
    margin-top: 70px;
  }
}
